body {
  font-family: "Open Sans", "Arial", sans-serif;
  margin: 0;
  padding: 0;
  background-color: #040a11;
  color: #fff;
  min-width: 567px; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

/* Container
===================== */
.container {
  width: 100%;
  width: 1024px;
  margin: 0 auto;
  padding: 0 15px; }

@media (max-width: 576px) {
  .container {
    width: 360px; } }

.goog-te-banner-frame.skiptranslate {
  display: none !important; }

body {
  top: 0px !important; }

.box {
  align-self: flex-end;
  animation-duration: 3s;
  animation-iteration-count: infinite; }

.move {
  animation-name: move;
  animation-timing-function: ease; }

@keyframes move {
  0% {
    transform: translateY(0); }
  50% {
    transform: translateY(-20px); }
  100% {
    transform: translateY(0); } }

.header {
  display: flex;
  width: 1156px;
  justify-content: space-between;
  margin: 0 auto;
  height: 100px;
  align-items: center;
  margin-bottom: 100px; }

@media (max-width: 576px) {
  .header {
    margin: 0 auto;
    width: 500px; } }

.header__flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 300px; }

.lang__list {
  width: 125px;
  display: flex;
  justify-content: space-between;
  list-style: none; }

.lang__list-item {
  cursor: pointer; }

.header__link {
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  width: 150px;
  text-decoration: none;
  font-size: 14px;
  color: #5c7a91; }

.header__link span {
  text-decoration: none;
  font-size: 20px;
  color: #5c7a91; }

.header__link:hover span {
  padding-left: 7px;
  transition: 0.2s; }

.language__img {
  padding-top: 6px;
  cursor: pointer;
  opacity: 0.5; }

.language__img:hover,
.language__img_active {
  opacity: 1; }

.section__top {
  position: relative;
  margin-bottom: 100px; }

.section__top::after {
  position: absolute;
  content: "";
  background: url(../img/bg1.jpg) center no-repeat;
  background-size: 1920 1150px;
  top: -230px;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 1150px;
  filter: opacity(90%); }

.section__top-title {
  margin: 0 auto;
  margin-bottom: 10px;
  text-align: center;
  font-family: 'BeaufortforLOL-Bold';
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 38px;
  /* identical to box height */
  text-align: center;
  text-transform: uppercase; }

.section__top-title span {
  color: #FEC000; }

.section__top-subtitle {
  margin: 0 auto;
  text-align: center;
  margin-bottom: 50px;
  font-family: 'BeaufortforLOL-Bold';
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 23px;
  /* identical to box height */
  text-align: center;
  text-transform: uppercase; }

#video-bg {
  position: absolute;
  top: -230px;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  z-index: -5;
  background-size: cover;
  width: 100%;
  margin: 0 auto; }

#video-bg > video {
  position: absolute;
  top: 0;
  left: 0;
  min-width: inherit;
  min-height: 100%; }

@supports (object-fit: cover) {
  #video-bg > video {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover; } }

@media (max-width: 1200px) {
  .section__top {
    width: 1200px; } }

@media (max-width: 990px) {
  #video-bg {
    display: none; }
  #video-bg > video {
    display: none; } }

@media (max-width: 576px) {
  .section__top {
    width: auto; }
  .section__top-title {
    width: 360px;
    font-size: 25px; }
  .section__top-subtitle {
    width: 360px;
    font-size: 13px; } }

.cards ul {
  display: flex;
  justify-content: space-between;
  list-style: none;
  width: 922px;
  margin: 0 auto;
  margin-bottom: 30px; }

.card__link {
  filter: grayscale(100%);
  font-family: "BeaufortforLOL-Bold";
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 18px; }

.card__link:hover {
  filter: grayscale(0%);
  transition: 0.5s; }

.card-item {
  position: absolute;
  width: 100%;
  height: 67px;
  background-color: rgba(0, 0, 0, 0.7);
  font-family: "BeaufortforLOL-Bold";
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 23px;
  color: #fff;
  text-align: center;
  padding-top: 20px;
  top: -150px;
  left: 0;
  z-index: 5; }

.card-item img {
  position: absolute;
  top: 8px;
  left: 45px; }

.card__link-text {
  position: absolute;
  top: -50px;
  left: 45px;
  z-index: 5;
  padding-top: 8px;
  width: 166px;
  height: 34px;
  background-color: #d4d4d4;
  color: #333;
  text-align: center; }

.card__link-text:hover {
  background-color: #fec000;
  transition: 0.2s; }

.card__link-text:hover span {
  padding-left: 5px;
  transition: 0.2s; }

@media (max-width: 576px) {
  .cards ul {
    flex-wrap: wrap;
    width: 360px; } }

.links {
  display: flex;
  justify-content: space-between;
  width: 550px;
  margin: 0 auto;
  margin-bottom: 30px; }

.links__item {
  text-decoration: none;
  width: 256px;
  height: 48px;
  background: rgba(8, 24, 40, 0.7);
  text-align: center;
  padding: 13px 0;
  border: 1px solid #384158;
  font-family: 'BeaufortforLOL-Bold';
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  text-align: center;
  color: #BBDDF6; }

.links__item:hover {
  background: #081828;
  transition: 0.3s;
  box-shadow: 0 0 30px rgba(78, 75, 75, 0.5); }

@media (max-width: 576px) {
  .links {
    flex-wrap: wrap;
    width: 250px; } }

.social ul {
  display: flex;
  justify-content: space-between;
  list-style: none;
  width: 280px;
  margin: 0 auto;
  padding: 0;
  margin-bottom: 48px; }

.social__img:hover {
  filter: opacity(70%); }

.description {
  position: relative;
  margin-bottom: 150px; }

@media (max-width: 1200px) {
  .description {
    width: 1200px; } }

.description::after {
  position: absolute;
  content: "";
  background: url(../img/bg2.jpg) center no-repeat;
  background-size: 1540px auto;
  top: -245px;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 1156px; }

.description__slide {
  width: 1024px;
  height: auto;
  min-height: 562px;
  background: rgba(4, 10, 16, 0.5);
  border-left: 1px solid #0f2e4d;
  border-right: 1px solid #0f2e4d;
  border-bottom: 1px solid #0f2e4d;
  padding-left: 65px;
  padding-top: 25px; }

.slide-none {
  display: none; }

.description__slider-title {
  font-size: 30px;
  color: #fec000;
  margin-top: 0;
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 28px; }

.description__slider-text {
  font-size: 18px;
  color: #fff;
  width: 870px;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 19px; }

.description__list {
  display: flex;
  margin: 0;
  padding: 0; }

.description__list-item {
  list-style: none; }

.btn {
  width: 249px;
  height: 64px;
  color: #fff;
  background: #07111c;
  border: 1px solid #0f2e4d;
  font-family: "BeaufortforLOL-Bold";
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 23px;
  /* identical to box height */
  text-align: center;
  outline: none; }

.btn-active {
  border-bottom: none;
  color: #fec000;
  background: rgba(4, 10, 16, 0.5); }

.plug {
  width: 277px;
  height: 64px;
  background: rgba(4, 10, 16, 0);
  border-bottom: 1px solid #0f2e4d; }

.description__sliders {
  margin-bottom: 50px; }

.description__link {
  display: block;
  margin: 0 auto;
  text-decoration: none;
  width: 256px;
  height: 48px;
  background: rgba(8, 24, 40, 0.5);
  text-align: center;
  padding: 13px 0;
  border: 1px solid #384158;
  font-family: "BeaufortforLOL-Bold";
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  text-align: center;
  color: #bbddf6; }

.description__link:hover {
  background: #081828;
  transition: 0.3s;
  box-shadow: 0 0 30px rgba(78, 75, 75, 0.5); }

@media (max-width: 576px) {
  .description__slide {
    width: 360px;
    padding: 15px; }
  .description {
    width: auto; }
  .description__slider-title {
    font-size: 20px;
    width: 350px; }
  .description__slider-text {
    font-size: 14px;
    width: 330px; }
  .description__sliders {
    width: 360px; }
  .btn {
    width: 120px; }
  .plug {
    width: 0; } }

.footer {
  width: 100%;
  height: 164px;
  background-color: #040a11; }

.footer__list {
  display: flex;
  justify-content: space-between;
  width: 490px;
  margin: 0 auto;
  margin-bottom: 30px;
  padding: 0; }

.footer__list-item {
  list-style: none;
  margin-bottom: 30px; }

.footer__list-item a {
  text-decoration: none;
  font-family: "BeaufortforLOL-Bold";
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  text-align: center;
  color: #5c7a90;
  opacity: 10; }

.footer__list-item:hover a {
  opacity: 0.5; }

.footer__text {
  width: 480px;
  margin: 0 auto;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  color: #5c7a90;
  margin-left: 150px;
  margin-right: 220px; }

.footer__block {
  display: flex;
  margin-left: 100px; }

.details__btn {
  opacity: 1; }

.details__btn:hover {
  opacity: 0.2; }

@media (max-width: 567px) {
  .footer__list {
    width: 330px; }
  .footer__text {
    width: 300px;
    margin-left: 0;
    margin-right: 0; }
  .footer__block {
    margin-left: 10px; } }

.details__btn {
  font-family: "BeaufortforLOL-Bold";
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  color: #5c7a91;
  text-align: center;
  cursor: pointer; }

.details__btn {
  width: 100px;
  margin: 0 auto; }
